@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";


html,body,.@{container-class}{
	height:100%;
}

.fn-@{nmq-ns}-error{
	background: #eee;
	height:100%;

	.pic{
		display: block;
		width: 28.4%;
		margin:27.5% auto 10%;
		background: data-uri('@{nmq-image-path}/error-bg.png') no-repeat;
		background-size: 100% 100%;

		&:before{
			padding-top:92.5%;
		}
	}
	.txt{
		color: #666;
		font-size:.64rem;
		text-align: center;
	}
}
