@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-my-nav{
	background: #fff;
	margin-bottom:1.4%;
	.banner{
		position: relative;
		background: @nmq-theme-color;
		&:before{
			content:'';
			display: block;
			padding-top:28%;
		}

		.logo{
			display: block;
			.absolute(50%,_,_,50%);
			width:18%;
			overflow: hidden;
			transform:translate(-50%,-70%);
			.img{
				border-radius: 100%;
				display: block;
				border:4px #f5c7d0 solid;
			}
		}
		.name{
			color:#fff;
			text-align: center;
			.absolute(73%,_,_,_);
			width:100%;
			.ellipsis();
		}
	}
	.nav{

		li{
			width:50%;
			float: left;
			text-align: center;
			padding:10px 0;
			span{
				padding:10px 8px;

			}

			&.active span{
				position: relative;
				border-bottom:2px @nmq-theme-color solid;
				&:before{
					content:'';
					display: block;
					border-style: solid solid dotted solid;
					border-color: transparent transparent @nmq-theme-color transparent;
					border-width:4px;
					width:0px;
					line-height: 0px;
					.absolute(_,_,0px,50%);
					margin-left:-4px;
				}
			}
		}
	}
}
